import React, {useEffect, useMemo, useState} from "react";
import styled from "styled-components";
import {Grid} from "@material-ui/core";

import AddInvestorModal from "../modal/AddInvestorModal";
import DataTable from "./DataTable";
import FilterSelection from "./FilterSelection";
import Button from "../../../components/Button";
import SearchBar from "../../../components/SearchBar";

import plus from '../../../assets/icons/Plus.svg';
import {getAllInvestorsData} from "../../../_helpers/serverApi";
import {prettyNumber} from "../../../_helpers/formattingNumbers";

interface IIndiviualInvestor {
    allocatedAtPrice: number,
    allocatedPercentage: number,
    change: number,
    changePercentage: number,
    currentBalance: number,
    email: string,
    id: string,
    investmentAtPrivateSale: number
    investor: string
    investorRanking: number
    profitLoss: number
    profitLossIndicator: true
    telegramUsername: string
    tokenAllocated: number
    tokenSold: number
    transfers: number
    walletAddress: string
}

const TableConatiner = () => {
    const [addInvestor, setAddInvestor] = useState<boolean>(false);

    const [selectedFilter, setSelectedFilter] = useState<'All' | 'Dumpers' | 'Holders'>('All')
    const [searchText, setSearchText] = useState<string>('');
    const [refreshData, setRefreshData] = useState<boolean>(true);

    const [data, setData] = useState<any>([])

    useEffect(() => {
        if (refreshData) {
            getAllInvestorsData()
                .then((d: { investors: any }) => {
                    console.log('getAllInvestorsData', d.investors);
                    setData(d.investors)
                    setRefreshData(false)
                })
                .catch((error) => {
                    console.log('getAllInvestorsData error', error);
                })
        }
    }, [refreshData])


    const filteredDataTemp = useMemo(() => {
        if (searchText === "") return data
        else {
            return data.filter((d: any) => {
                return (
                    d.investor.toString().toLowerCase().includes(searchText.toLowerCase()) ||
                    prettyNumber(d.allocatedPercentage).toString().toLowerCase().includes(searchText.toLowerCase()) ||
                    prettyNumber(d.currentBalance).toString().toLowerCase().includes(searchText.toLowerCase()) ||
                    prettyNumber(d.tokenSold).toString().toLowerCase().includes(searchText.toLowerCase()) ||
                    d.profitLoss.toString().toLowerCase().includes(searchText.toLowerCase()) ||
                    prettyNumber(d.change).toString().toLowerCase().includes(searchText.toLowerCase()) ||
                    prettyNumber(d.allocatedMAHA).toString().toLowerCase().includes(searchText.toLowerCase())
                )})
        }
    }, [searchText, data])

    return (
        <MainTableContainer>
            <AddInvestorModal
                openModal={addInvestor}
                onClose={() => setAddInvestor(false)}
                onSuccess={() => {
                    setRefreshData(true)
                    setAddInvestor(false)
                }}
            />
            <section className="marginB16">
                <Grid container spacing={2}>
                    <Grid item lg={5} xl={5} md={5} sm={12} xs={12}>
                        {/*<FilterSelection*/}
                        {/*    selectedFilter={selectedFilter}*/}
                        {/*    onChange={(data) => setSelectedFilter(data)}*/}
                        {/*/>*/}
                    </Grid>
                    <Grid item lg={1} xl={1} md={1} sm={12} xs={12}>
                    </Grid>
                    <Grid item lg={4} xl={4} md={4} sm={12} xs={12}>
                        <SearchBar
                            ILabelValue={''}
                            value={searchText}
                            DefaultValue={'Search by Investor name, Address, Balance, etc.'}
                            setText={(val) => setSearchText(val)}
                            />
                    </Grid>
                    <Grid item lg={2} xl={2} md={2} sm={12} xs={12}>
                        <Button onClick={() => setAddInvestor(true)} variant={'outlined'} size={"sm"}>
                            <div className="single-line">
                                <img src={plus} alt={'plus'}/>
                                <div style={{marginLeft: '5px'}}>Add Investor</div>
                            </div>
                        </Button>
                    </Grid>
                </Grid>
            </section>

            <DataTable
                data={filteredDataTemp}
            />
        </MainTableContainer>
    )
}

export default TableConatiner

const MainTableContainer = styled.div`
  margin-bottom: 40px;
`