import CustomModal from "../../../components/CustomModal";
import CustomInputContainer from "../../../components/CustomInputConatiner";
import {useState} from "react";
import {Grid} from "@material-ui/core";
import Button from "../../../components/Button";
import {addNewInvestor} from "../../../_helpers/serverApi";
import TextWrapper from "../../../components/TextWrapper";
import theme from "../../../theme";
import {correctString, ValidateNumber} from "../../../components/CustomInputConatiner/inputValidation";


interface IPorps {
    openModal: boolean;
    onClose: () => void;
    onSuccess: () => void;
}

const AddInvestorModal = (props: IPorps) => {
    const {openModal, onSuccess, onClose} = props

    const [name, setName] = useState<string>('');
    const [walletAddress, setwalletAddress] = useState<string>('');
    const [emailId, setemailId] = useState<string>('');
    const [teleId, setteleId] = useState<string>('');
    const [investmentAtPrivateSale, setinvestmentAtPrivateSale] = useState<string>('0');
    const [tokenAllocated, settokenAllocated] = useState<string>('0');
    const [allocatedAtPrice, setallocatedAtPrice] = useState<string>('0');

    const [errorMsg, setErrorMsg] = useState<string>('');
    const [addingInvestor, setAddingInvestor] = useState<boolean>(false)

    // var disabled: boolean = name !== '' ||
    //     walletAddress !== '' ||
    //     emailId !== '' ||
    //     teleId !== ''
        // Number(investmentAtPrivateSale) > 0 ||
        // Number(tokenAllocated) > 0 ||
        // Number(allocatedAtPrice) > 0

    const resetFields = () => {
        setName('')
        setemailId('')
        setwalletAddress('')
        setteleId('')
        settokenAllocated('0')
        setallocatedAtPrice('0')
        setinvestmentAtPrivateSale('0')
        setErrorMsg('')
    }

    const onNumericalValueChange = (value: string) => {
        if (value === '') {
        } else {
            if (!ValidateNumber(value)) return null
        }

        if (Number(value) && Number(value) <= 0) return null;
        return (value.length > 1 ? correctString(value) : value);
    }

    const validateFields = () => {
        return true
    }

    const addInvestor = () => {
        if (validateFields() && errorMsg === '') {
            const body = {
                "name": name,
                "email": emailId,
                "walletAddress": walletAddress,
                "tokenAllocated": Number(tokenAllocated) || 0,
                "allocatedAtPrice": Number(allocatedAtPrice) || 0,
                "telegramUsername": teleId,
                "investmentAtPrivateSale": Number(investmentAtPrivateSale) || 0,

                "currentBalance": 0,
                "tenMinutePriorBalance": 0,
                "countryCode": 91,
                "phoneNumber": 7756885345,
                "profitLoss": 0,
                "proftLossIndicator": false,
                "change": 0,
            }
            setAddingInvestor(true)
            addNewInvestor(body)
                .then((data) => {
                    console.log('addNewInvestor', data)
                    if (data?.error) {
                        alert(data?.error)
                    } else {
                        onSuccess();
                    }
                })
                .catch((error) => {
                    console.log('addNewInvestor error', error)
                    alert('Internal Server Error, Try Again later')
                })
                .finally(() => {
                    resetFields()
                    setAddingInvestor(false)
                })
        }

    }

    return (
        <CustomModal
            closeButton
            handleClose={() => {
                resetFields()
                onClose()
            }}
            open={openModal}
            modalTitleStyle={{}}
            modalContainerStyle={{}}
            modalBodyStyle={{}}
            title={`Add Investor`}
        >
            <section>
                <CustomInputContainer
                    ILabelValue={'Investor Name'}
                    DefaultValue={'Eg: Elon Musk'}
                    setText={(data) => setName(data)}
                    value={name}
                />
                <CustomInputContainer
                    ILabelValue={'Wallet Address'}
                    DefaultValue={'Eg: 0xcab3e009Ff3d670.....75e81C8E4E2452606E8'}
                    setText={(data) => setwalletAddress(data)}
                    value={walletAddress}
                />
                <CustomInputContainer
                    ILabelValue={'Email Id'}
                    DefaultValue={'Eg: Elon Musk'}
                    setText={(data) => setemailId(data)}
                    value={emailId}
                />
                <CustomInputContainer
                    ILabelValue={'Telegram Id'}
                    DefaultValue={'Eg:https://telegram.com/@<InvestorId>'}
                    setText={(data) => setteleId(data)}
                    value={teleId}
                />
                <CustomInputContainer
                    ILabelValue={'Total Investment at private sale'}
                    DefaultValue={'$ Some Dollars'}
                    setText={(data) => {
                        if (onNumericalValueChange(data) !== null) setinvestmentAtPrivateSale(data)
                    }}
                    value={investmentAtPrivateSale.toString()}
                />
                <CustomInputContainer
                    ILabelValue={'Number of token allocated'}
                    DefaultValue={'Some MAHA'}
                    setText={(data) => {
                        if (onNumericalValueChange(data) !== null) settokenAllocated(data)
                    }}
                    value={tokenAllocated.toString()}
                />
                <CustomInputContainer
                    ILabelValue={'Maha Price at Token Allocated'}
                    DefaultValue={'$ Some Dollars'}
                    setText={(data) => {
                        if (onNumericalValueChange(data) !== null) setallocatedAtPrice(data)
                    }}
                    value={allocatedAtPrice.toString()}
                />
            </section>
            <section>
                <TextWrapper
                    text={errorMsg}
                    Fcolor={theme.color.red[300]}
                />
            </section>
            <section>
                <Grid container spacing={2} style={{paddingTop: '12px'}}>
                    <Grid item lg={6} xl={6} md={6} sm={12} xs={12}>
                        <Button
                            variant={'transparent'}
                            onClick={() => {
                                resetFields()
                                onClose()
                            }}
                        >
                            Cancel
                        </Button>
                    </Grid>
                    <Grid item lg={6} xl={6} md={6} sm={12} xs={12}>
                        <Button
                            onClick={() => addInvestor()}
                            disabled={addingInvestor}
                        >{addingInvestor? 'Adding Investor': 'Add Investor'}</Button>
                    </Grid>
                </Grid>
            </section>
        </CustomModal>
    )
}

export default AddInvestorModal;