import {callApi} from './apicaller'

export const getAllInvestorsData = async () => {
    return await callApi('investor')
}

export const getAllocationSummary = async () => {
    return await callApi('tracker/stats');
}

export const addNewInvestor = async (body: any) => {
    return await callApi('investor', 'post', body)
}

export const getParticularInvestorData = async (body: any) => {
    return await callApi('particular/investor', 'post', body)
}

export const deleteParticularInvestor = async (body: any) => {
    return await callApi('investor', 'delete', body)
}

export const getInvestorTransactions = async (body: any) => {
    return await callApi('investor/alltransactions', 'post', body)
}