import React, {useState} from 'react'
import { DataGrid, GridToolbarContainer, GridToolbarExport } from '@material-ui/data-grid';
import { makeStyles } from '@material-ui/core/styles';
import clsx from "clsx";
import ArrowDropUpIcon from '@material-ui/icons/ArrowDropUp';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';

import '../../../customCss/Custom-Mahadao-Data-Table.css'
import {prettyNumber, truncateMiddle} from "../../../_helpers/formattingNumbers";
import theme from "../../../theme";
import {format} from "timeago.js";
import {ITransaction} from "./TableContainer";
import TextWrapper from "../../../components/TextWrapper";


export function SortedDescendingIcon() {
    return <ArrowDropUpIcon className="icon" style={{color: 'white'}}/>;
}
  
export function SortedAscendingIcon() {
    return <ArrowDropDownIcon className="icon" style={{color: 'white'}}/>;
}

function CustomToolbar() {
    return (
        <GridToolbarContainer>
            <GridToolbarExport />
        </GridToolbarContainer>
    );
}

const useStyles = makeStyles({
    root: {
        '& .super-app-theme--header': {
            cursor: 'pointer',
        },
        '& .super-app.negative': {
            color: theme.color.red[300],
        },
        '& .super-app.positive': {
            color: theme.color.green[300],
        },
        '& .super-app.loss': {
            color: theme.color.red[300],
        },
        '& .super-app.profit': {
            color: theme.color.green[300],
        },
    },
    gridStyle: {
        color: 'white'
    }
});

interface Iprops {
    data: ITransaction[]
}

const DataTable = (props: Iprops) => {

    const {data = []} = props;
    const classes = useStyles();

    const columns = [
        {
            field: 'transactionHash', headerName: 'TRANSACTION HASH', flex: 0.2, sortable: true,
            renderCell: ((params: any) => {
                return `${truncateMiddle(params.value, 15, '...')}`
            }),
            cellClassName: 'fontWeight300'
        },
        {
            field: 'timestamp', headerName: 'Time', flex: 0.15, sortable: true,
            renderCell: ((params: any) => {
                return `${format(params.value)}`
            }),
            cellClassName: 'fontWeight300'
        },
        { field: 'action', headerName: 'ACTION', flex: 0.1, sortable: true,
            cellClassName: (params: any) => clsx('fontWeight300','super-app', {
                negative: params.value === 'sell',
                positive: params.value === 'buy',
            }),
            renderCell: ((params: any) => {
                return `${params.value.toUpperCase()}`
            }),
        },
        {
            field: 'balance', headerName: 'BALANCE', flex: 0.1, sortable:true,
            renderCell: ((params: any) => {
                return prettyNumber(params.value)
            })
        },

        {
            field: 'price', headerName: 'PRICE', flex: 0.15, sortable: true,
            renderCell: ((params: any) => {
                return `$${prettyNumber(params.value)}`
            })
            // cellClassName: (params: any) => clsx('super-app', {
            //     negative: parseFloat(params.value) < 0,
            //     positive: parseFloat(params.value) > 0,
            // }),

        },
        {
            field: 'value', headerName: 'VALUE', flex: 0.15, sortable: true,
            renderCell: ((params: any) => {
                return `$${prettyNumber(params.value)}`
            })
            // cellClassName: (params: any) => clsx('super-app', {
            //     loss: parseFloat(params.value) < 0,
            //     profit: parseFloat(params.value) > 0,
            // }),
            // valueFormatter: (params : any) => {
            //     return `$ ${parseFloat(params.value)}`
            // }
        },
        { field: 'changeInBalance', headerName: 'CHANGE IN BALANCE', flex: 0.2, sortable: true,
            cellClassName: (params: any) => clsx('super-app', {
                negative: params.row.action === 'sell' || params.row.action === 'swap',
                positive: params.row.action === 'buy',
            }),
            renderCell: ((params: any) => {
                return `${prettyNumber(params.value)} %`
            })
        },
    ];

    const [rows, setRows] = useState<number>(10);

    return (
        <React.Fragment>
            <div className={classes.root} style={{position: 'relative'}}>
                <div className="viewAll" onClick={() => setRows(data.length)}>
                    <TextWrapper text={'View All'}/>
                </div>
                <DataGrid className={classes.gridStyle}
                    getRowId={(rows) => {
                        return rows.id
                    }}
                    rows={data}
                    columns={columns}
                    pageSize={rows}
                    disableMultipleSelection={true}
                    onRowClick={(newSelection) => {
                        window.open(`https://etherscan.io/tx/${newSelection.row.transactionHash}`, "_blank");
                    }}
                    // onCellClick={async (params) => {
                    //     if (params.field === 'highlightURL') {
                    //         setUrl(params?.value)
                    //     }

                    // }}
                    autoHeight={true}
                    disableColumnMenu={true}
                    components={{
                        Toolbar: CustomToolbar,
                        ColumnSortedDescendingIcon: SortedDescendingIcon,
                        ColumnSortedAscendingIcon: SortedAscendingIcon,
                    }}
                  disableColumnSelector={true}
                  disableSelectionOnClick={true}
                />
            </div>

        </React.Fragment>
    )
}

export default DataTable
