import React, {useEffect, useMemo, useState} from "react";
import {Grid} from "@material-ui/core";
import styled from "styled-components";

import SearchBar from "../../../components/SearchBar";
import Button from "../../../components/Button";
import chartIcon from "../../../assets/icons/Graph.svg";
import ProfileDataTable from "./ProfileDataTable";
import TextWrapper from "../../../components/TextWrapper";
import {getInvestorTransactions} from "../../../_helpers/serverApi";
import {prettyNumber} from "../../../_helpers/formattingNumbers";
import {format} from "timeago.js";

interface Iprops {
    walletAddress: string;
}

export interface ITransaction {
    action: "buy" | "sell" | "swap",
    balance: number,
    changeInBalance: number,
    mahaAmount: number,
    price: number
    timestamp: number,
    transactionHash: string,
    value: number,
    walletAddress: string,
}

const TableContainer = (props: Iprops) => {
    const {walletAddress} = props

    const [searchText, setSearchText] = useState<string>('');

    const [data, setData] = useState<ITransaction[]>([])

    useEffect(() => {
        getInvestorTransactions({walletAddress: walletAddress})
            .then((data) => {
                console.log('getInvestorTransactions', data.transactions);
                setData(data.transactions);
            })
    }, [walletAddress])

    const CleanedData = data.map((d: any) => {
        return {
            ...d,
            'id': d.transactionHash
        }
    })

    const filteredDataTemp = useMemo(() => {
        if (searchText === "") return CleanedData
        else {
            return CleanedData.filter((d: any) => {
                return (
                    d.transactionHash.toString().toLowerCase().includes(searchText.toLowerCase()) ||
                    format(d.timestamp).toString().toLowerCase().includes(searchText.toLowerCase()) ||
                    d.action.toString().toLowerCase().includes(searchText.toLowerCase()) ||
                    prettyNumber(d.balance).toString().toLowerCase().includes(searchText.toLowerCase()) ||
                    prettyNumber(d.price).toString().toLowerCase().includes(searchText.toLowerCase()) ||
                    prettyNumber(d.value).toString().toLowerCase().includes(searchText.toLowerCase()) ||
                    prettyNumber(d.changeInBalance).toString().toLowerCase().includes(searchText.toLowerCase())
                )})
        }
    }, [searchText, data])

    return (
        <MainTableContainer>
            <section className="marginB16">
                <Grid container spacing={2}>
                    <Grid item lg={5} xl={5} md={5} sm={12} xs={12}>
                        <div style={{marginTop: '8.5px'}}>
                            <TextWrapper
                                text={'Transactions'}
                                fontWeight={600}
                                fontSize={18}
                            />
                        </div>
                    </Grid>
                    <Grid item lg={2} xl={2} md={2} sm={12} xs={12}>
                    </Grid>
                    <Grid item lg={5} xl={5} md={5} sm={12} xs={12}>
                        <SearchBar
                            ILabelValue={''}
                            value={searchText}
                            DefaultValue={'Search by Investor name, Address, Balance, etc.'}
                            setText={(val) => setSearchText(val)}
                        />
                    </Grid>
                    {/*<Grid item lg={2} xl={2} md={2} sm={12} xs={12}>*/}
                    {/*    <Button onClick={() => {}} variant={'outlined'} size={"sm"}>*/}
                    {/*        <div className="single-line">*/}
                    {/*            <img src={chartIcon} alt={'chart'}/>*/}
                    {/*            <div style={{marginLeft: '5px'}}>Visual Map</div>*/}
                    {/*        </div>*/}
                    {/*    </Button>*/}
                    {/*</Grid>*/}
                </Grid>
            </section>

            <ProfileDataTable
                data={filteredDataTemp}
            />
        </MainTableContainer>
    )
}

export default TableContainer;

const MainTableContainer = styled.div`
  margin-bottom: 40px;
`