// @ts-nocheck
import * as _ from 'underscore';

const millify = (
    uglyNumber: number | string,
    decimalPoints: number,
): string => {
    // Make sure value is a number
    const ugly = (num => {
        if (typeof num !== 'number') {
            throw new Error('Input value is not a number');
        } else {
            return parseFloat(String(num));
        }
    })(uglyNumber);

    // Figure out how many digits in the integer
    const digits = Math.floor(Math.log10(Math.abs(ugly))) + 1;

    // Figure out the appropriate unit for the number
    const units = ((num, zeroes) => {
        let zeroes2 = zeroes;
        const keys = _.keys(suffixes);
        for (const z of keys) {
            if (num > Number(z)) {
                zeroes2 = Number(z);
            }
        }
        return { suffix: suffixes[String(zeroes2)], zeroes: zeroes2 };

    })(digits, 2);

    const pretty = ugly / Math.pow(10, units.zeroes);

    const decimal = pretty % 1 === 0 ? 2 : Math.max(1, decimalPoints + 1) || 3;

    if (ugly > -1000 && ugly < 1000) {
        return String(ugly);
    }
    return `${parseFloat(prettyNumber(pretty, decimal))}${units.suffix}`;
};


export const formatDecimals = (passedNumber: number | string) => {
    const formattedBalance = String(passedNumber)
    const decimalsPointIndex = formattedBalance.indexOf('.');
    if (decimalsPointIndex === -1) return formattedBalance;
    return formattedBalance.slice(0, decimalsPointIndex) + '.' + formattedBalance.slice(decimalsPointIndex + 1, decimalsPointIndex + 1 + 3);
}

export const prettyNumber = (value: number, decimals = 3) => {
    // const getFormatDecimals = formatDecimals(value)
    return Number(value).toLocaleString('en-US', {
        maximumFractionDigits: decimals,
    })
}

// @ts-nocheck

const suffixes: any = {
    '3': 'K',
    '12': 'T',
    '6': 'M',
    '9': 'B',
};

export const prettify = (_num: number | string) => {
    const num = Number(_num);

    if (Math.abs(num) >= 10000) return millify(num, 2);
    if (Math.abs(num) >= 1000) return millify(num, 2);

    // if (Math.abs(num) >= 100) {
    //   return num.toFixed(2);
    // }
    // if (Math.abs(num) >= 1) {
    //   return num.toFixed(3);
    // }
    // if (Math.abs(num) >= 0.1) {
    //   return num.toFixed(4);
    // }

    return Math.floor(num) || 0;
}

export const truncateMiddle = function (
    fullStr: string = '12345678922500025',
    strLen: number,
    separator?: string,
) {
    if (fullStr.length <= strLen) return fullStr;

    separator = separator || '...';

    var sepLen = separator.length,
        charsToShow = strLen - sepLen,
        frontChars = Math.ceil(charsToShow / 3),
        backChars = Math.floor(charsToShow / 3);

    return fullStr.substr(0, frontChars) + separator + fullStr.substr(fullStr.length - backChars);
};
