import React, {useState} from "react";
import styled from "styled-components";

import theme from "../../../../theme";
import leftIcon from "../../../../assets/icons/LeftArrowIcon.svg";
import chartIcon from "../../../../assets/icons/Graph.svg";
import TextWrapper from "../../../../components/TextWrapper";
import PieRepresentation from "./PieRepresentation";
import LineFormatRepresentation from "./LineFormatRepresentation";
import {IParticularInvestor} from "../../index";
import {colorList} from "../../../Dashboard/components/AllocationOverview";

const dummy = [{
    name: 'USDC',
    amount: 100,
    percentage: 50,
},{
    name: 'USDT',
    amount: 1000,
    percentage: 50,
},
]

interface Iprops {
    data: { data: IParticularInvestor }
}

const Index = (props: Iprops) => {
    const {data} = props
    const [chartFormat, setChartFormat] = useState<boolean>(false);

    const dataTotal = data.data.tokenSold + data.data.tokenAllocated + data.data.currentBalance

    const calculatedData = [{
        name: 'Total Tokens allocated',
        amount: data.data.tokenAllocated,
        amountInUsd: data.data.allocationPrice,
        percentage: data.data.tokenAllocated/dataTotal,
        color: colorList[0],

    },{
        name: 'Total Wallet Balance',
        amount: data.data.currentBalance,
        amountInUsd: data.data.currentBalance,

        percentage:  data.data.currentBalance/dataTotal,
        color: colorList[1],

    },{
        name: 'Total Tokens Sold',
        amount: data.data.tokenSold,
        amountInUsd: data.data.tokenSold,
        percentage:  data.data.tokenSold/dataTotal,
        color: colorList[2],

    },
    ]

    return (
        <MainCardContainer className="custom-mahadao-material1" onClick={() => setChartFormat(!chartFormat)}>
            <section className="marginB20">
                <SwitchButton className="single-line-center-start">
                    <img src={chartFormat? leftIcon: chartIcon} alt={'icon'} className="marginR08"/>
                    <TextWrapper
                        text={chartFormat? 'Back to normal view': 'View Chart to analyze Investor allocation'}
                        fontSize={12}
                        fontWeight={600}
                    />
                </SwitchButton>
            </section>
            <section>
                {chartFormat? (<PieRepresentation
                    balances={calculatedData}
                />): (
                    <LineFormatRepresentation
                        data={{data: data.data}}
                    />
                )}
            </section>
        </MainCardContainer>
    )
}

export default Index;

const MainCardContainer = styled.div`
  padding: 24px;
`

const SwitchButton = styled.div`
  padding: 4px 6px;
  background: ${theme.color.transparent[500]};
  border-radius: 4px;
  width: max-content;
  cursor: pointer;
`