import React, {useEffect, useState} from 'react'
import { DataGrid, GridToolbarContainer, GridToolbarExport } from '@material-ui/data-grid';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import ArrowDropUpIcon from '@material-ui/icons/ArrowDropUp';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';

import '../../../customCss/Custom-Mahadao-Data-Table.css'
import messageIcon from "../../../assets/icons/Message.svg";
import {history} from "../../../_helpers/history";
import {prettyNumber} from "../../../_helpers/formattingNumbers";
import theme from "../../../theme";
import TextWrapper from "../../../components/TextWrapper";


export function SortedDescendingIcon() {
    return <ArrowDropUpIcon className="icon" style={{color: 'white'}}/>;
  }
  
export function SortedAscendingIcon() {
    return <ArrowDropDownIcon className="icon" style={{color: 'white'}}/>;
}

function CustomToolbar() {
    return (
        <GridToolbarContainer>
            <GridToolbarExport />
        </GridToolbarContainer>
    );
}

const useStyles = makeStyles({
    root: {
        '& .super-app-theme--header': {
            cursor: 'pointer',
        },
        '& .super-app.negative': {
            color: theme.color.red[300],
            fontWeight: '600',
        },
        '& .super-app.positive': {
            color: theme.color.green[300],
            fontWeight: '600',
        },
        '& .super-app.loss': {
            color: theme.color.red[300],
            fontWeight: '600',
        },
        '& .super-app.profit': {
            color: theme.color.green[300],
            fontWeight: '600',
        },
    },
    gridStyle: {
        color: 'white'
    }
});

interface IProps {
    data: []
}

const DataTable = (props: IProps) => {
    const {data = []} = props

    const classes = useStyles();

    const columns = [
        {
            field: 'investorRanking', headerName: 'Rank', flex: 0.05, sortable: true,
        },
        {
            field: 'investor', headerName: 'INVESTORS', flex: 0.2, sortable: true,
            cellClassName: 'fontWeight300'
        },
        { field: 'allocatedPercentage', headerName: 'ALLOCATED', flex: 0.15, sortable: true,
            renderCell: ((params: any) => {
                return `${prettyNumber(params.row.tokenAllocated)} (${prettyNumber(params.value)}%)`
            })
        },
        {
            field: 'currentBalance', headerName: 'WALLET BALANCE ', flex: 0.15, sortable:true,
            renderCell: ((params: any) => {
                return prettyNumber(params.value)
            })
        },

        {
            field: 'tokenSold', headerName: 'Token Sold', flex: 0.15, sortable: true,
            cellClassName: (params: any) => clsx('super-app', {
                negative: true,
            }),
            renderCell: ((params: any) => {
                return prettyNumber(params.value)
            })

        },
        {
            field: 'profitLoss', headerName: 'PROFIT/LOSS', flex: 0.15, sortable: true,
            cellClassName: (params: any) => clsx('super-app', {
                negative: !params.row.profitLossIndicator,
                positive: params.row.profitLossIndicator,
            }),
            // cellClassName: (params: any) => clsx('super-app', {
            //     loss: parseFloat(params.value) < 0,
            //     profit: parseFloat(params.value) > 0,
            // }),
            // valueFormatter: (params : any) => {
            //     return `$ ${parseFloat(params.value)}`
            // }
        },
        { field: 'changePercentage', headerName: 'CHANGE', flex: 0.1, sortable: true,
            cellClassName: (params: any) => clsx('super-app', {
                negative: true,
            }),
            renderCell: ((params: any) => {
                return `${prettyNumber(params.value)} %`
            })
        },
        // { field: 'mail', headerName: ' ', flex: 0.1, sortable: false,
        //     renderCell: (params: any) => (
        //     <div style={{textAlign: 'center'}} className="w-100">
        //         <img src={messageIcon} alt={'icon'} height={20} className="pointer"/>
        //     </div>),
        // }
    ];

    const [rows, setRows] = useState<number>(10);

    return (
        <React.Fragment>

            <div className={classes.root} style={{position: 'relative'}}>
                <div className="viewAll" onClick={() => setRows(data.length)}>
                    <TextWrapper text={'View All'}/>
                </div>
                <DataGrid className={classes.gridStyle}
                    getRowId={(rows) => {
                        return rows.id
                    }}
                    rows={data}
                    columns={columns}
                    pageSize={rows}
                    disableMultipleSelection={true}
                    // onRowClick={(newSelection) => {
                    //     const win = window.open(`${newSelection.row.videoUrl}`, "_blank");
                    //     win.focus()
                    // }}
                    onCellClick={async (params) => {
                        if (params.field === 'investor') {
                            history.push(`/investor-profile/${params.row.walletAddress}`)
                            history.go(0)

                        }
                    }}
                    autoHeight={true}
                    disableColumnMenu={true}
                    components={{
                        Toolbar: CustomToolbar,
                        ColumnSortedDescendingIcon: SortedDescendingIcon,
                        ColumnSortedAscendingIcon: SortedAscendingIcon,
                    }}
                  disableColumnSelector={true}
                  disableSelectionOnClick={true}
                />
            </div>

        </React.Fragment>
    )
}

export default DataTable
