import styled from "styled-components";
import {Grid} from "@material-ui/core";

import PageHeader from "../../components/PageHeader";
import InfoBox from "./components/InfoBox";
import TableConatiner from "./components/TableConatiner";
import AllocationOverview from "./components/AllocationOverview";
import {useEffect, useState} from "react";
import {getAllocationSummary} from "../../_helpers/serverApi";
import {prettify} from "../../_helpers/formattingNumbers";

export interface IAllocationSummaryData {
    "investorsCount": number,
    "totalInvestment": number,
    "currentValue": number,
    "investorProfit": number,
    "investorProfitPercentage": number,
    "tokenAllocated": number,
    "totalTokensAllocatedPrice": number,
    "totalTokenSold": number,
    "totalTokenSoldPrice": number,
    "totalWalletBalance": number,
    "totalWalletBalancePrice": number
}

const InitAllocationSummaryData = {
    "investorsCount": 0,
    "totalInvestment": 0,
    "currentValue": 0,
    "investorProfit": 0,
    "investorProfitPercentage": 0,
    "tokenAllocated": 0,
    "totalTokensAllocatedPrice": 0,
    "totalTokenSold": 0,
    "totalTokenSoldPrice": 0,
    "totalWalletBalance": 0,
    "totalWalletBalancePrice": 0
}

const Dashboard = () => {
    const [allocationSummaryData , setAllocationSummaryData] = useState<IAllocationSummaryData>(InitAllocationSummaryData);

    useEffect(() => {
        getAllocationSummary()
            .then((data: IAllocationSummaryData) => {
                setAllocationSummaryData(data);
            })
    }, [])


    return (
        <Page>
            <PageHeader text={'MahaDAO Investors Tracker'}/>
            <div className="custom-container">
                <section className="marginB16">
                    <Grid container spacing={2}>
                        <Grid item lg={6} xl={6} md={6} sm={12} xs={12}>
                            <AllocationOverview
                                data={allocationSummaryData}
                            />
                        </Grid>
                        <Grid item lg={6} xl={6} md={6} sm={12} xs={12}>
                            <InfoBox
                                data1={{
                                    title: 'Active Investors',
                                    value: `${prettify(allocationSummaryData.investorsCount)}`,
                                    percent: '',
                                    status: 'positive'
                                }}
                                data2={{
                                    title: 'Total Investment',
                                    value: `$${prettify(allocationSummaryData.totalInvestment)}`,
                                    percent: '',
                                    status: 'positive',
                                }}
                                data3={{
                                    title: 'Current Value',
                                    value: `$${prettify(allocationSummaryData.totalInvestment)}`,
                                    percent: '',
                                    status: 'positive'
                                }}
                                data4={{
                                    title: 'Total Investor Profit',
                                    value: `$${prettify(allocationSummaryData.investorProfit)}`,
                                    percent: '',
                                    status: 'positive'
                                }}
                            />
                        </Grid>
                    </Grid>
                </section>
                <TableConatiner/>
            </div>
        </Page>
    )
}

export default Dashboard;

export const Page = styled.div`
  margin-top: 80px;
  min-height: calc(100vh - 80px);
`

