import React, { PureComponent } from 'react';
import { PieChart, Pie, Cell } from 'recharts';
import theme from "../../theme";

interface IProps {
    balances: {
        name: string;
        amount: number;
        percentage: number;
    }[];
}

const colorList = [theme.color.primary[500],
    theme.color.primary[400],
    theme.color.primary[300],
    theme.color.primary[200],
    theme.color.primary[100]
]

export default class CustomPieChart extends PureComponent<IProps> {
    render() {
        const data = this.props.balances.map((b, i) => {
            return ({
                name: b.name,
                value: b.percentage,
                color: colorList[i],
            })
        });

        return (
            <PieChart width={160} height={160}>
                <Pie
                    data={data}
                    dataKey="value"
                    nameKey="name"
                    cx="50%"
                    cy="50%"
                    innerRadius={60}
                    outerRadius={80}
                    fill="#82ca9d"
                >
                    {
                        data.map((entry, index) => (
                            <Cell key={`cell-${index}`} fill={entry.color} stroke={entry.color} />
                        ))
                    }
                </Pie>
            </PieChart>
        );
    }
}
