import { useEffect, useState } from "react";
import { Grid } from "@material-ui/core";
import styled from "styled-components";

import CustomInputContainer, {
  ICStatesInterface,
} from "../../../components/CustomInputConatiner";
import { Page } from "../../Dashboard";
import Button from "../../../components/Button";
import TextWrapper from "../../../components/TextWrapper";
import { history } from "../../../_helpers/history";

const Login = () => {
  const [emailId, setEmailID] = useState<string>("");
  const [password, setPassword] = useState<string>("");
  const [ICStates, setICStates] = useState<ICStatesInterface>({
    IState: "default",
    IMsg: "",
  });

  useEffect(() => {
    localStorage.setItem("userLoggedIn", "false");
  }, []);

  const loginUser = () => {
    if (emailId === "admin@mahadao.com" && password === "mahadao") {
      localStorage.setItem("userLoggedIn", "true");
      history.push("/dashboard");
      history.go(0);
      setICStates({
        IState: "default",
        IMsg: "",
      });
    } else {
      setICStates({
        IState: "error",
        IMsg: "Invalid emailid or password",
      });
    }
    setPassword("");
    setEmailID("");
  };

  return (
    <Page>
      <section className="paddingT40">
        <Grid container justify={"center"}>
          <Grid item lg={4} sm={10} xs={12}>
            <LoginContainer className="custom-mahadao-material1">
              <div className="text-center marginB40">
                <TextWrapper
                  text={"Sign In"}
                  fontFamily={"Syne"}
                  fontSize={24}
                  fontWeight={"bold"}
                />
              </div>
              <CustomInputContainer
                ILabelValue={"Email id"}
                value={emailId}
                DefaultValue={"eg: john@xyz.com"}
                setText={(data) => setEmailID(data)}
                Istate={ICStates.IState}
                Imsg={ICStates.IMsg}
              />
              <CustomInputContainer
                ILabelValue={"Password"}
                value={password}
                DefaultValue={"*******"}
                setText={(data) => setPassword(data)}
                inputMode={"password"}
              />
              <ButtonContainer>
                <Button onClick={() => loginUser()}>Login</Button>
              </ButtonContainer>
            </LoginContainer>
          </Grid>
        </Grid>
      </section>
    </Page>
  );
};

export default Login;

const LoginContainer = styled.div`
  padding: 32px;
`;

const ButtonContainer = styled.div`
  margin-top: 32px;
`;
