import React, { CSSProperties, useEffect, useState } from 'react';
import styled from 'styled-components';
import { InputBase } from '@material-ui/core';

type props = {
    ILabelValue: string;
    value: string;
    DefaultValue: string;
    disabled?: boolean;
    setText: (val: string) => void;
    inputMode?: 'none' | 'text' | 'tel' | 'url' | 'email' | 'numeric' | 'decimal' | 'search' | 'password';
    DisableMsg?: string;

    dontShowBackgroundContainer?: boolean;
    backgroundColor?:string;

    Istate?: 'default' | 'error' | 'warning';
    Imsg?: string;
    IWarningstate?: 'default' | 'warning';
    warningMsg?: string;
    errorCallback?: (flag: boolean) => void;

};

export interface ICStatesInterface {
    IState: 'default' | 'error' | 'warning';
    IMsg: string;
}

const CustomInputContainer: React.FC<props> = (props) => {
    const {
        ILabelValue,
        DefaultValue,
        inputMode = 'text',
        disabled = false,
        Istate = 'default',
        Imsg = '',
        DisableMsg = '',
        IWarningstate = 'default',
        warningMsg = '',
        dontShowBackgroundContainer = true,
        backgroundColor = '',
    } = props;

    const [ICStates, setICStates] = useState<ICStatesInterface>({ IState: Istate, IMsg: Imsg });
    const [ICWarningStates, setICWarningStates] = useState<ICStatesInterface>({ IState: IWarningstate, IMsg: warningMsg });

    //For ref not to be removed
   /* const temp: ICStatesInterface = {
        IState: 'error',
        IMsg: 'Amount cannot be more than your balance.',
    };
    setICStates(temp);
    props.errorCallback(true);*/

    useEffect(() => {
        let temp = { IState: Istate, IMsg: Imsg };
        setICStates(temp);
    }, [Istate, Imsg]);


    const IConatinerStyle = () => {
        let returnObj: CSSProperties = {}
        if (dontShowBackgroundContainer) {
            returnObj['padding'] = '0px';
            returnObj['backgroundColor'] = 'transparent';
        }
        if (DisableMsg !== "") {
            returnObj['opacity'] = "0.32";
        }
        return returnObj;
    }

    const onValueChange = (value: string) => {
        props.setText(value);
    }

    return (
        <div style={{ position: 'relative' }}>
            {DisableMsg !== '' && <TotalDisable><DMsg>{DisableMsg}</DMsg></TotalDisable>}
            <IConatiner style={IConatinerStyle()}>
                {ILabelValue !== '' &&
                    (<ILabelContainer>
                        <ILabelLeft>
                            <ILabel>{ILabelValue}</ILabel>
                        </ILabelLeft>
                    </ILabelContainer>)
                }
                <IFieldConatiner className={`input-${ICStates.IState}`} style={backgroundColor !== ''? {backgroundColor: backgroundColor}: {}}>
                    <InputBase
                        // inputMode={props?.inputMode}
                        placeholder={DefaultValue}
                        value={props.value}
                        inputProps={{ 'aria-label': 'naked' }}
                        style={{
                            padding: '8px 12px 6px 12px',
                            color: '#FFFFFF',
                            flex: 1,
                            fontFamily: 'Inter !important',
                        }}
                        disabled={disabled}
                        type={inputMode}
                        onChange={(event) => onValueChange(event.target.value)}
                    />
                </IFieldConatiner>
                {ICStates.IMsg !== '' && (
                    <p className={`input-font-${ICStates.IState}`}>{ICStates.IMsg}</p>
                )}
                {ICWarningStates.IMsg !== '' && (
                    <p className={`input-font-warning`}>{ICWarningStates.IMsg}</p>
                )}
            </IConatiner>
        </div>
    );
};

export default CustomInputContainer;

const TotalDisable = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.16);
  box-sizing: border-box;
  border-radius: 12px;
  z-index: 12;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #FFFFFF29;
`;

const DMsg = styled.p`
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: #FFFFFF;
  margin-bottom: 0;
`

const IConatiner = styled.div`
  background: rgba(255, 255, 255, 0.08);
  border-radius: 8px;
  padding: 12px;
  margin-bottom: 20px;
`;
const ILabelContainer = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 12px;
`;
const ILabelLeft = styled.div`
  display: flex;
  align-items: center;
  flex: 1;
`;

const ILabel = styled.p`
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  color: rgba(255, 255, 255, 0.64);
  margin-bottom: 0;
`;

const IFieldConatiner = styled.div`
  display: flex;
  flex-direction: row;
  background: #151414;
  border-radius: 6px;
  max-height: 44px;
`;





