import React, {useEffect, useState} from "react";
import {Grid} from "@material-ui/core";
import { useParams } from 'react-router-dom';

import avatar from '../../assets/common/avatar.svg'
import TextWrapper from "../../components/TextWrapper";
import styled from "styled-components";
import Button from "../../components/Button";
import plus from "../../assets/icons/Plus.svg";
import close from "../../assets/icons/Close.svg";
import LeftTopCard from "./Components/TopLeftCard";
import TopRightCard from "./Components/TopRightCard";
import TableContainer from "./Components/TableContainer";
import {deleteParticularInvestor, getParticularInvestorData} from "../../_helpers/serverApi";
import {history} from "../../_helpers/history";
import CustomModal from "../../components/CustomModal";

export interface IParticularInvestor {
    allocatedAtPrice: number,
    allocatedPercentage: number,
    change: number,
    changePercentage: number,
    currentBalance: number,
    email: string,
    id: string,
    investmentAtPrivateSale: number,
    investor: string,
    investorRanking: number,
    profitLoss: number,
    profitLossIndicator: boolean
    telegramUsername: string,
    tokenAllocated: number,
    tokenSold: number,
    transfers: number,
    walletAddress: string,
    currentBalancePrice: number,
    investorOutOf: number,
    profitLossPercentage: number,
    allocationPrice: number,
}

const InitParticularInvestor = {
    allocatedAtPrice: 0,
    allocatedPercentage: 0,
    change: 0,
    changePercentage: 0,
    currentBalance: 0,
    email: '',
    id: '',
    investmentAtPrivateSale: 0,
    investor: '',
    investorRanking: 0,
    profitLoss: 0,
    profitLossIndicator: true,
    telegramUsername: '',
    tokenAllocated: 0,
    tokenSold: 0,
    transfers: 0,
    walletAddress: '',
    currentBalancePrice: 0,
    investorOutOf: 0,
    profitLossPercentage: 0,
    allocationPrice: 0,
}

const InvestorProfile = () => {
    // @ts-ignore
    const { walletAddress } = useParams();
    const [data, setData] = useState<IParticularInvestor>(InitParticularInvestor)
    const [confirmationModal, setConfirmationModal] = useState<boolean>(false);

    useEffect(() => {
        getParticularInvestorData({walletAddress: walletAddress})
            .then((data) => {
                console.log('getParticularInvestorData', data)
                setData(data)
            })
            .catch((error) => {
                console.log('getParticularInvestorData error', error)
                alert('internal server error')
            })
    }, [])

    const removeInvestor = () => {
        deleteParticularInvestor({walletAddress: walletAddress})
            .then((data) => {
                console.log('deleteParticularInvestor', data)
                setData(data)
                history.push('/dashboard')
                history.go(0)
            })
            .catch((error) => {
                console.log('deleteParticularInvestor error', error)
                alert('internal server error')
            })
    }

    return (
        <Page>
            <CustomModal
                title={'Are you sure you want to delete this investor?'}
                handleClose={() => setConfirmationModal(false)}
                open={confirmationModal}
            >
                <Grid container spacing={2}>
                    <Grid item lg={6} xl={6} md={6} sm={12} xs={12}>
                        <Button onClick={() => setConfirmationModal(false)} variant={'transparent'}>Cancel</Button>
                    </Grid>
                    <Grid item lg={6} xl={6} md={6} sm={12} xs={12}>
                        <Button onClick={() => removeInvestor()}>Yes</Button>
                    </Grid>
                </Grid>

            </CustomModal>
            <div className="custom-container">
                <section style={{marginTop: '120px'}}>
                    <div className="single-line-center-start">
                        <img src={avatar} alt={'img'} height={88} className="rounded-circle"/>
                        <div className="marginL32">
                            <TextWrapper
                                text={data.investor}
                                fontWeight={'bold'}
                                fontSize={24}
                            />
                            <ButtonConatiner>
                                <a href={`mailto:${data.email}`} >
                                    <Button onClick={() => {}} variant={"outlined"}>
                                        <img src={plus} alt={'plus'}/>
                                        <div style={{marginLeft: '5px'}}>Message</div>
                                    </Button>
                                </a>
                                <div className="marginL12">
                                    <Button onClick={() => setConfirmationModal(true)} variant={"outlined"}>
                                        <img src={close} alt={'remove'}/>
                                        <div style={{marginLeft: '5px'}}>Remove</div>
                                    </Button>
                                </div>
                            </ButtonConatiner>
                        </div>
                    </div>
                </section>
                <section className="marginT40">
                    <Grid container spacing={2}>
                        <Grid item lg={6} xl={6} md={6} sm={12} xs={12}>
                            <LeftTopCard
                                data={{data: data}}
                            />
                        </Grid>
                        <Grid item lg={6} xl={6} md={6} sm={12} xs={12}>
                            <TopRightCard
                                data={{data: data}}
                            />
                        </Grid>
                    </Grid>
                </section>

                <section className="marginT40">
                    <TableContainer
                        walletAddress={walletAddress}
                    />
                </section>
            </div>
        </Page>
    )
}

export default InvestorProfile

const Page = styled.div`
  margin-top: 80px;
  min-height: calc(100vh - 80px);
`

const ButtonConatiner = styled.div`
  max-width: 122px;
  margin-top: 8px;
  display: flex;
`
